import React, { useState, useRef, useEffect } from 'react'
import { Link } from 'react-router-dom'
import AnchorLink from 'react-anchor-link-smooth-scroll-v2'
import ButtonLink from '../../components/buttonLink/ButtonLink'
import Popup from '../../components/popup/Popup'
import './css/style.css'
const popupData = [
  {
    id: 0,
    title: ' ',
    img: '/img/img1.png',
    list: [' ', ' ', ' ', '', '      (. )', '-', 'VIP-']
  },
  {
    id: 1,
    title: '   IT',
    img: '/img/img2.png',
    list: ['   ', 'IT-  ', ' IT-']
  }
]

export default function Main() {
  const form = useRef()

  const [popup, setPopup] = useState(false)
  const [popupContent, setPopupContent] = useState([])

  function openPopup(e, number) {
    e.preventDefault()
    if (number === 1) {
      setPopupContent(popupData[0])
    }
    if (number === 2) {
      setPopupContent(popupData[1])
    }

    setPopup(true)
  }


  return (
    <>

      <section className="section section1">
        <div className="container">
          <h1 className="section1__title">      </h1>
          <p className="section1__text">     ,    
                ,      </p>
          <div className='section1__link'>
            <AnchorLink href={'#form'}><ButtonLink linkText={' '} class_name={'section1__link'} /></AnchorLink>
          </div >
        </div>
      </section>
      <section className="section2 section">
        <div className="container">
          <h4 className="section2__subtitle"> </h4>
          <a href="/" className="section2__superlink" onClick={e => openPopup(e, 1)}> </a>
          <a href="/" className="section2__superlink" onClick={e => openPopup(e, 2)}>   IT</a>
        </div>
      </section>
      <section className="section section3">
        <div className="container">
          <h3 className="section3__itle title">  </h3>
          <div className="section3__flex-wrapper">
            <p className="section3__text_l">     ,
                 </p>
            <p className="section3__text text">            , -
                 .  --       . 
                   
                 
               . --     ,    ,  ,       </p>
          </div>
          <div className="section3__flex-blocks">
            <div className="section3__block section3__block_1">
              <h4 className="section3__block-title">23</h4>
              <p className="section3__text text">  </p>
            </div>
            <div className="section3__block section3__block_2">
              <h4 className="section3__block-title">150+</h4>
              <p className="section3__text text"> 
                     </p>
            </div>
          </div>
          <h4 className="section3__subtitle"> </h4>
          <p className="section3__text_40 text">    
               ,  
             </p>
        </div>
      </section>
      <section className="section section4">
        <div className="container">
          <h3 className="section4__title title">    
             -</h3>
          <div className="section4__flex-wrapper">
            <div className="section4__block">
              <p className="section4__text text">      
                  ,     </p>
            </div>
            <div className="section4__block">
              <p className="section4__text text"> 
                         </p>
            </div>
            <div className="section4__block">
              <p className="section4__text text">            ,    </p>
            </div>
          </div>
        </div>
      </section>
      <section className="section section5" id='form'>
        <div className="container">
          <div className="section5__flex-wrapper">
            <div className="section5__flex-block">
              <h3 className="section5__title title"> ?</h3>
              <p className="section5__text">        </p>
            </div>
            <div className="section5__flex-block">
              <form action="#" className="section5__form form2" id="form2" ref={form}>
                <input type="text" name="name" id="name2" placeholder='' />
                <input type="tel" name="phone" id="phone2" placeholder='' />
                <div className="section5__flex-wrapper2">
                  <button className='form2__button' type='submit'></button>
                  <p className="form2__text text">  ,      <a href="/" className="form2__conf">  </a></p>
                </div>

              </form>
            </div>
          </div>
        </div>

      </section>
      {/* <div className="popup-wrapper" > */}
      <Popup popup={popup} setPopup={setPopup} content={popupContent} />
      {/* </div> */}


    </>
  )
}
